* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

button {
  background: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

:root {
  --blue: #299ded;
  --header-dark: #1b1e21;
  --bg-main: #303539;
  --grey: #606365;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: var(--bg-main);
  padding-top: 80px;
}

.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  height: inherit;
}

/* scroll style */
::-webkit-scrollbar {
  width: 5px;
  background-color: var(--bg-main);
  border-radius: 15px;
}
::-webkit-scrollbar-thumb {
  background-color: var(--grey);
  width: 3px;
  border-radius: 15px;
}
